import PageTemplate from '@itcode/template/app/PageTemplate';
import RootTemplate from '@itcode/template/app/RootTemplate';

import { WrapPageElementBrowserArgs, WrapRootElementBrowserArgs } from 'gatsby';
import { ReactNode } from 'react';

import './src/_app.scss';

/**
 *   ReactNode  
 *
 * @param {WrapRootElementBrowserArgs} param0: WrapRootElementBrowserArgs 
 *
 * @returns {ReactNode} ReactNode
 */
export function wrapRootElement(props: WrapRootElementBrowserArgs): ReactNode
{
	return (
		<RootTemplate {...props} />
	);
}

/**
 *   ReactNode  
 *
 * @param {WrapPageElementBrowserArgs} param0: WrapPageElementBrowserArgs 
 *
 * @returns {ReactNode} ReactNode
 */
export function wrapPageElement(props: WrapPageElementBrowserArgs): ReactNode
{
	return (
		<PageTemplate {...props} />
	);
}