import { INFO } from '@itcode/tools/env';

export interface MetaTemplateProps
{
	/**
	 * 
	 */
	title: string;

	/**
	 * 
	 */
	description?: string;

	/**
	 *  
	 */
	type?: string;

	/**
	 *  URL
	 */
	url?: string;

	/**
	 *  
	 */
	image?: string;

	/**
	 * 
	 */
	locale?: string;
}

/**
 *  template  JSX   
 *
 * @param {MetaTemplateProps} param0: MetaTemplateProps 
 *
 * @returns {JSX.Element} JSX
 */
export default function MetaTemplate({ title, description = '    ', type = 'website', url = '/', image = 'https://user-images.githubusercontent.com/50317129/259803524-1354ec8d-c02f-44cc-a49c-2d5ea53452b6.png', locale = 'ko_KR' }: MetaTemplateProps): JSX.Element
{
	const fullTitle = `${title} - ${INFO.title}`;

	return (
		<>
			<title>{fullTitle}</title>

			{description ? <meta content={description} name='description' /> : null}

			<meta content={INFO.title} property='og:site_name' />
			<meta content={fullTitle} property='og:title' />
			{description ? <meta content={description} property='og:description' /> : null}
			<meta content={type} property='og:type' />
			<meta content={url} property='og:url' />
			{image ? <meta content={image} property='og:image' /> : null}
			<meta content={locale} property='og:locale' />

			<link href={url} rel='canonical' />
		</>
	);
}