import { CSSProperties, DetailedHTMLProps, ImgHTMLAttributes, useMemo } from 'react';

export interface HitsProps extends DetailedHTMLProps<ImgHTMLAttributes<HTMLImageElement>, HTMLImageElement>
{
	/**
	 * 
	 */
	unique: string;

	/**
	 *  
	 */
	countBgcolor?: CSSProperties['backgroundColor'];

	/**
	 *  
	 */
	titleBgcolor?: CSSProperties['backgroundColor'];

	/**
	 * 
	 */
	icon?: string;

	/**
	 *  
	 */
	iconColor?: CSSProperties['backgroundColor'];

	/**
	 * 
	 */
	text: string;

	/**
	 *  
	 */
	isFlat?: boolean;
}

/**
 * Hits atom  JSX  
 *
 * @param {HitsProps} param0: HitsProps 
 *
 * @returns {JSX.Element} JSX
 */
export default function Hits({ unique, countBgcolor, titleBgcolor, icon, iconColor, text, isFlat, ...props }: HitsProps): JSX.Element
{
	const src = useMemo(() =>
	{
		const obj = {
			count_bg: countBgcolor,
			edge_flat: isFlat,
			icon,
			icon_color: iconColor,
			title: text,
			title_bg: titleBgcolor,
			url: process.env.NODE_ENV === 'development' ? 'https://dev.itcode.dev' : unique
		};

		const param = Object
			.entries(obj)
			.filter((i) => i[1])
			.map(([ k, v ]) => `${k}=${encodeURIComponent(v || '')}`)
			.join('&');

		return `https://hits.seeyoufarm.com/api/count/incr/badge.svg?${param}`;
	}, [ unique, countBgcolor, titleBgcolor, icon, iconColor, text, isFlat ]);

	return (
		<img alt={text} data-component='Hits' src={src} {...props} />
	);
}