import { PaletteMode } from '@mui/material';
import { AlertColor } from '@mui/material/Alert';
import { SnackbarOrigin } from '@mui/material/Snackbar';
import { create } from 'zustand';

export type SetThemeHandler = (theme: PaletteMode) => void;
export type ToggleThemeHandler = () => void;

export interface ThemeStateProps
{
	/**
	 * 
	 */
	theme: PaletteMode;

	/**
	 *   
	 */
	setTheme: SetThemeHandler;

	/**
	 *   
	 */
	toggle: ToggleThemeHandler;
}

export interface ToastProps
{
	/**
	 * 
	 */
	title?: string;

	/**
	 *  
	 */
	severity?: AlertColor;

	/**
	 * 
	 */
	duration?: number;

	/**
	 * 
	 */
	anchorOrigin?: SnackbarOrigin;

	/**
	 * 
	 */
	content: JSX.Element;
}

export type SetToastHandler = (toast?: ToastProps) => void;

export interface ToastStateProps
{
	/**
	 * 
	 */
	toast?: ToastProps;

	/**
	 *   
	 */
	setToast: SetToastHandler;
}

export type ProjectType = Queries.GetAllProjectQuery['allMarkdownRemark']['edges'][number]['node']['frontmatter'];
export type SetProjectHandler = (project?: ProjectType) => void;

export interface ProjectStateProps
{
	/**
	 * 
	 */
	project?: ProjectType;

	/**
	 *   
	 */
	setProject: SetProjectHandler;
}

export const useThemeState = create<ThemeStateProps>((set) => ({
	setTheme: (theme): void =>
	{
		localStorage.setItem('theme', theme);
		set({ theme });
	},
	theme: 'light',
	toggle: (): void =>
	{
		set(({ theme }) =>
		{
			const newTheme = (theme === 'light' ? 'dark' : 'light');

			localStorage.setItem('theme', newTheme);
			return ({ theme: newTheme });
		});
	}
}));

export const useToastState = create<ToastStateProps>((set) => ({
	setToast: (toast): void =>
	{
		set({ toast });
	}
}));

export const useProjectState = create<ProjectStateProps>((set) => ({
	setProject: (project): void =>
	{
		set({ project });
	}
}));