import { useGetProceedProject } from '@itcode/graphql';
import ProjectFeed from '@itcode/organism/global/ProjectFeed';

import Button from '@mui/material/Button';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';
import { Link } from 'gatsby';

/**
 *   organism  JSX  
 *
 * @returns {JSX.Element} JSX
 */
export default function ResearchBox(): JSX.Element
{
	const list = useGetProceedProject();

	return (
		<Stack alignItems='center' data-component='ResearchBox' spacing={10} width='100%'>
			<Typography fontWeight='bold' letterSpacing={10} variant='h4'> </Typography>

			<ProjectFeed list={list} />

			<Stack alignItems='center' display='inline-flex' spacing={2}>
				<Typography variant='h5'>  ?</Typography>

				<Link to='/projects'>
					<Button size='large'>
						<Typography variant='h5'>   </Typography>
					</Button>
				</Link>
			</Stack>
		</Stack>
	);
}