import { graphql, useStaticQuery } from 'gatsby';

/**
 *     
 *
 * @returns {Queries.GetAllProjectQuery}   
 */
export function useGetAllProject(): Queries.GetAllProjectQuery
{
	const query = useStaticQuery<Queries.GetAllProjectQuery>(graphql`
	query GetAllProject {
		allMarkdownRemark(sort: {fileAbsolutePath: DESC}) {
			edges {
				node {
					id
					frontmatter {
						title
						author
						subtitle
						languages
						images
						created
						completed
						links {
							url
							type
							name
						}
						icon
					}
				}
			}
			totalCount
		}
	}
	`);

	return query;
}